html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}

.page {
  display: flex; 
  flex-direction: column; 
  min-height: 100%;
}

.content {
  padding: 2vmin; 
  flex-grow: 1;
}

header {
  flex-grow: 0;
  background-color: #c9dcff;
  color: #9d71e8; 
  display: flex; 
  padding: 1vmin 5vmin 1vmin 2vmin;
}

.navigation {
  flex-grow: 1; 
  display: flex; 
  justify-content: flex-end; 
}

.navigation ul {
  display: flex;
  gap: 1em;
  list-style-type: none;
}

footer {
  flex-grow: 0;
  background-color: #c9dcff;
  color: #333; 
  display: flex; 
  justify-content: flex-end; 
  gap: 1vmin;
  padding: 1vmin 5vmin 1vmin 2vmin;
}

footer h2 {
  font-size: 100%;
}

.section {
  margin-bottom: 2em; 
}

.headline {
  display: flex; 
  align-items: center; 
  gap: 1em;
}

.headline .icon {
  height: 1em; 
}

.logo {
  height: 4vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

